import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/major/qicheyunyong/教学改革项目数量（院级以上）.json'
export default class Jiaoxuegaige extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        教学改革项目数量（院级以上）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData} color={1}></Barchart>
                    </div>
                    {/* <div className='item-info'>
                        当前值：5.2/生，比目标值高23%
                                </div> */}
                </div>
            </div>
        )
    }
}
