import Zhuanyezongxuefen from './component/school/Zhuanyezongxuefen'
import Gaojizhiwujiaoshizhanbi from './component/school/Gaojizhiwujiaoshizhanbi'
import Yanjiushengxueweijiaoshizhanbi from './component/school/Yanjiushengxueweijiaoshizhanbi'

// 专业
import Major from './component/major';

class Dabiao extends Component {
    render() {
        let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: (currentSlide, nextSlide) => {
                this.props.changeCarouselTitle(nextSlide);
                if (nextSlide === 1){
                    this.props.filterMajor("1");
                    this.props.toggleMajorMenu(true);
                }else {
                    this.props.toggleMajorMenu(false);
                }
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
        return (
            <Carousel  ref={slider => (this.slider = slider)} {...settings} >
            {/* 学校 */}
                <div>
                    <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
                        <Dangfeishoujiao></Dangfeishoujiao>
                        <Jicengzuzhi></Jicengzuzhi>
                        <Sizhengkejiao></Sizhengkejiao>
                        <Zhuanrenjiaoshifuguo></Zhuanrenjiaoshifuguo>
                        <Guowaizuzhi></Guowaizuzhi>
                        <Zhandimianji></Zhandimianji>
                        <Tushuzongliang></Tushuzongliang>
                        <Jiaozhigong></Jiaozhigong>
                        <Zhaunrenxingbie></Zhaunrenxingbie>
                        <Zhaunrenshuliang></Zhaunrenshuliang>
                        <Gaojizhiwu></Gaojizhiwu>
                        <Zhaoshengzhuanye></Zhaoshengzhuanye>
                        <Nannvbili></Nannvbili>
                        <Shengneishengyuan></Shengneishengyuan>
                        <Jinengchoucha></Jinengchoucha>
                        <Shengjihuojiang></Shengjihuojiang>
                        <Shengjihuiyi></Shengjihuiyi>
                        <Jishufuwu></Jishufuwu>
                        <Zhuanlizongshu></Zhuanlizongshu>
                        <Lunwenzongshu></Lunwenzongshu>
                        <Jiaocaizongshu></Jiaocaizongshu>
                        <Keyanhuojiang></Keyanhuojiang>
                        <Zuzhijigou></Zuzhijigou>
                        <Lingdaobanzi></Lingdaobanzi>
                        <Zhuanxiangjingfei></Zhuanxiangjingfei>
                        <Xuexiaojingfei></Xuexiaojingfei>
                        <Xinzengkeyan></Xinzengkeyan>
                        <Shengjunzhandi></Shengjunzhandi>
                        <Shengjunsushe></Shengjunsushe>
                        <Xingzhengyongfang></Xingzhengyongfang>
                        <Shengjunshijian></Shengjunshijian>
                        <Jisuanji></Jisuanji>
                        <Canyupingjiao></Canyupingjiao>
                        <Tingke></Tingke>
                        <Zoufangshixi></Zoufangshixi>
                        <Chuangyehuojiang></Chuangyehuojiang>
                        <Zhuanlishubili></Zhuanlishubili>
                        <Jinengjingsai></Jinengjingsai>
                        <Sanhaoyouxiusheng></Sanhaoyouxiusheng>
                        <Dinggangshixi></Dinggangshixi>
                        <Gongweishu></Gongweishu>
                        <Gongongkecheng></Gongongkecheng>
                        <Jieshoudinggang></Jieshoudinggang>
                        <Hezuoqiyeshuliang></Hezuoqiyeshuliang>
                        <Jinengchouchachengji></Jinengchouchachengji>
                        <Jishufuwudaokuane></Jishufuwudaokuane>
                        <Jianzhijiaoshi></Jianzhijiaoshi>
                        <Jiangxuejinhuojiang></Jiangxuejinhuojiang>
                        <Jiaoshihuojiang></Jiaoshihuojiang>
                        <Jingpinzaixian></Jingpinzaixian>
                        <Kaishekecheng></Kaishekecheng>
                        <Kaoshitongguolv></Kaoshitongguolv>
                        <Lilunxueshishu></Lilunxueshishu>
                        <Jiaocaishuliang></Jiaocaishuliang>
                        <Kechengshuliang></Kechengshuliang>
                        <Shehuipeixun></Shehuipeixun>
                        <Shengjunyiqi></Shengjunyiqi>
                        <Shijianxueshishu></Shijianxueshishu>
                        <Shixunshishiyonglv></Shixunshishiyonglv>
                        <Xiandaixuetuzhi></Xiandaixuetuzhi>
                        <Xiaowaishixun></Xiaowaishixun>
                        <Xinshengbaodao></Xinshengbaodao>
                        <Xueshengchuangxin></Xueshengchuangxin>
                        <Xueshengjineng></Xueshengjineng>
                        <Xueshengjingwai></Xueshengjingwai>
                        <Zaixiaoshengrenshu></Zaixiaoshengrenshu>
                        <Zhichengjiegou></Zhichengjiegou>
                        <Zhuanrenjiaoshi></Zhuanrenjiaoshi>
                        <Zhuanrenjiaoshishuliang></Zhuanrenjiaoshishuliang>
                        <Zhuanyejiaoxue></Zhuanyejiaoxue>
                        <Zhuanyezongxuefen></Zhuanyezongxuefen>
                        <Gaojizhiwujiaoshizhanbi></Gaojizhiwujiaoshizhanbi>
                        <Yanjiushengxueweijiaoshizhanbi></Yanjiushengxueweijiaoshizhanbi>
                    </div>
                </div>
                {/* 专业 */}
                <div>
                    <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>
                        <Major></Major>
                    </div>
                </div>
            
            </Carousel>
        )
    }
}

export default connect(null, 
(dispatch) => {
    return {
        changeCarouselTitle(type){
            const action = carouselTitleAction(type);
            dispatch(action);
        },
        filterMajor(type){
            const action = majorMenuAction(type);
            dispatch(action);
        },
        toggleMajorMenu(bool){
            const action = isShowMajorMenuAction(bool);
            dispatch(action);
        }
    }
})(Dabiao)
